import React from 'react';
import './App.css';
import {Navbar, NavbarBrand} from 'reactstrap';
import Parks from './pages/Parks';
import Activities from './pages/Activities';
import {Switch, Route} from 'react-router-dom';;

class App extends React.Component {					
	constructor(props){
		super(props);
		
		this.state = {
			parks: [],
      DataisLoaded: false
		};
	}
  componentDidMount() {
    fetch("https://developer.nps.gov/api/v1/parks?parkCode=acad&api_key=WaKkmlfgbOg9OwQkoHOOKLW89LedGlCEnJ5u90Z4")
    .then((res) => res.json())
    .then((json) => {
      this.setState({
        parks: json,
        DataisLoaded: true
      });
    })
  }
  render(){
      const {DataisLoaded, parks} = this.state;
      if (!DataisLoaded) return <div>
        <h1> Please wait some time...</h1></div>;

      return(
        <div className = "App">
          <Navbar color = "primary">
            <div className = "container" id = "brand">
              <NavbarBrand> National Parks</NavbarBrand>
            </div>
          <Switch>
            <Route exact path ='/' render={() => <App/>} />
            <Route exact path ="/Parks" render= {() => <Parks/>} />
            <Route exact path ="/Activities" render= {() => <Activities/>} />
          </Switch>
          </Navbar>
            ))
        </div>
      )

  }
}
export default App;
