import React from 'react'

const parks = ({parks}) =>{
    return (
        <div>
            <center><h1>National Parks</h1></center>
            {parks.map((park) =>
            (
                <div class = "park">
                    <div class = "park-body">
                        <h5 class = "park-name">{park.name}</h5>
                        <h6 class = "park-code">{park.code}</h6>
                        <p class = "park-description">{park.description}</p>
                    </div>
                </div>
            ))}
        </div>
    )
}

export default parks
